/*
    @package CashMoney-NFT
    --------------------------------------------------
    |   MAIN LAYOUT STYLES                           |
    --------------------------------------------------
*/

body {
    background: #404040;
    background-image: url("./images/desktop_marble.png");
    background-repeat: repeat;
    background-size: cover;
    min-height: 100vh;
    margin: 0;
    overflow-x: hidden;
    padding: 0;
}

main {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    width: 90%;
}
.border-height-5 {
    content: '';
    height: 5px;
    margin: 0 auto;
    width: 90%;
}
.border-height-7 {
    content: '';
    height: 7px;
    margin: 0 auto;
    width: 90%;
}
.border-height-10 {
    content: '';
    height: 10px;
    margin: 0 auto;
    width: 90%;
}

.border-token-height-50 {
    background: url("./images/token_bg.png") 0 0 repeat-x;
    background-size: contain;
    content: '';
    height: 60px;
    margin: 10px auto;
    width: 90%;
}



.gold-gradient {
    background: linear-gradient(to right, rgb(216,143,40) 0%, rgb(251, 241, 125) 19.8529%, rgb(214, 151, 40) 33.5784%, rgb(165, 114, 42) 47.3039%, rgb(245, 204, 106) 68.1373%, rgb(214, 151, 40) 87.0098%, rgb(245, 204, 106) 100%);
}
